import React from 'react'
import BgVideo from '../assets/bg.mkv'
import {Outlet} from 'react-router-dom'

function Layout() {
    return (
        <>
            <video className={'bg-video'} src={BgVideo} autoPlay muted loop id="myVideo"/>
            <section className={'wrapper'}>
                <Outlet/>
            </section>
        </>
    )
}

export default Layout