import React from 'react'
import {Link} from 'react-router-dom'
import {motion} from 'framer-motion'

function NeonButton({children, path, ...props}) {
    return (
        <motion.span
            {...props}
            key={path}
        >
            <Link className={'neon-link'} to={'/' + path}>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                {children}
            </Link>
        </motion.span>
    )
}

export default NeonButton