import React from 'react'
import Camera from '../../components/Camera'
import {Preview} from '../../components/Preview'
import {useDispatch, useSelector} from 'react-redux'
import {useNavigate} from 'react-router-dom'
import {loginUser, setAuthError, setScreenshot} from './authSlice'
import {setFacenetMessage, setOutline} from './facenetSlice'
import {Alert} from '../../components/Alert'

function Login() {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const screenshot = useSelector((state) => state.auth.request.screenshot)
    const faces = useSelector((state) => state.facenet.faces)

    if (faces.length > 0) {
        setTimeout(() => {
            const user = {screenshot, descriptor: Object.values(faces[0].descriptor)}
            dispatch(loginUser(user)).then(payload => {
                if (payload.meta.requestStatus === 'fulfilled') {
                    return navigate('/dashboard', {replace: true})
                } else {
                    setTimeout(() => {
                        dispatch(setAuthError({login: {serverErr: null}}))
                        dispatch(setOutline('#ddd'))
                        dispatch(setFacenetMessage('Place the face in the oval.'))
                        dispatch(setScreenshot(null))
                    }, 3000)
                }
            })
        }, 1500)
    }

    return (
        <div className={'login-container'}>
            <Alert/>
            {screenshot && <Preview/>}
            {!screenshot && <Camera/>}
        </div>
    )
}

export default Login