import React, {useEffect, useRef, useState} from 'react'
import Webcam from 'react-webcam'
import {useDispatch, useSelector} from 'react-redux'
import {getFacenetMessage, getOutline, setFacenetMessage, setOutline} from '../features/Login/facenetSlice'
import * as faceapi from 'face-api.js'
import {setScreenshot} from '../features/Login/authSlice'
import {motion} from 'framer-motion'

function Camera() {
    const dispatch = useDispatch()
    const message = useSelector(getFacenetMessage)
    const outline = useSelector(getOutline)
    const detection = useRef()
    const [hasCamera, setHasCamera] = useState(false)
    const cameraRef = useRef(null)

    const takeScreenshot = () => {
        dispatch(setScreenshot(cameraRef.current.getScreenshot()))
        clearInterval(detection.current)
    }
    const handleStreamVideo = async (e) => {
        await faceapi.nets.tinyFaceDetector.loadFromUri('facenet/models/tiny_face_detector')
        let counter = 0
        detection.current = setInterval(async () => {
            if (counter <= 40) {
                const faces = await faceapi.detectAllFaces(cameraRef.current.video, new faceapi.TinyFaceDetectorOptions())
                if (faces.length === 1 && faces[0].score > 0.5) {
                    counter++
                    dispatch(setOutline('#00ff00'))
                    dispatch(setFacenetMessage('Stand still for ' + Math.round(4 - (counter / 10)) + ' seconds.'))
                } else {
                    counter = 0
                    dispatch(setOutline('#f00000'))
                    dispatch(setFacenetMessage('Place the face in the oval.'))
                }
            } else {
                takeScreenshot()
            }
        }, 100)
    }
    const handleCameraError = () => {
        // dispatch(setCameraStatus('closed'))
        // const err = {}
        // err[activeTab] = {serverErr: 'There was a problem accessing the WEBCAM. Grant permission and reload the page.'}
        // dispatch(setAuthError(err))
    }

    useEffect(() => {
        navigator.getUserMedia({
            video: true
        }, () => {
            setHasCamera(true)
        }, () => {
            setHasCamera(false)
        })
    }, [])

    return (
        <motion.div
            initial={{opacity: 0, scale: 0.2}}
            animate={{opacity: 1, scale: 1}}
            exit={{opacity: 0, scale: 0.2}}
            transition={{duration: 0.6}}
            style={{textAlign: 'center'}}>
            {hasCamera && <motion.p
                initial={{opacity: 0, y: -100}}
                animate={{opacity: 1, y: 0}}
                exit={{opacity: 0, y: -100}}
                transition={{duration: 0.5}}
                className={'face-message'}>{message}</motion.p>}
            <div className={'camera-container'}>
                {hasCamera && <><Webcam
                    id={'webcam'}
                    audio={false}
                    ref={cameraRef}
                    screenshotFormat="image/jpeg"
                    screenshotQuality={1}
                    mirrored={true}
                    videoConstraints={{facingMode: 'user'}}
                    onUserMedia={handleStreamVideo}
                    onUserMediaError={handleCameraError}
                />
                    <div className="face-overlay" style={{borderColor: outline}}></div>
                </>}
            </div>
        </motion.div>
    )
}

export default Camera