import {useSelector} from 'react-redux'
import {getFacenetError} from '../features/Login/facenetSlice'
import {motion} from 'framer-motion'


export const AlertRegister = () => {
    const errorAuth = useSelector(state => state.auth.error)
    const errorFacenet = useSelector(getFacenetError)
    const request = useSelector(state => state.auth.request)

    const getAuthErrorList = (tab) => Object.values(errorAuth[tab]).filter(e => e != null)
    const getErrorList = errorFacenet == null ? getAuthErrorList('register') : getAuthErrorList('register').concat(errorFacenet)

    return (
        <motion.div
            initial={{opacity: 0, y: -100}}
            animate={{opacity: 1, y: 0}}
            exit={{opacity: 0, y: -100}}
            transition={{delay: 0.5}}
        >
            <div
                className={'alert alert-success ' + (request.code === 200 && getErrorList.length === 0 ? '' : 'hidden')}
                role="alert">
                <ul>
                    <li>{request.msg}</li>
                </ul>
            </div>
            <div className={'alert alert-danger ' + (getErrorList.length === 0 ? 'hidden' : '')} role="alert">
                <ul>
                    {getErrorList.map((e, i) => {
                        if (Array.isArray(e)) {
                            return e.map((j, y) => <li key={y}>{j}</li>)
                        } else {
                            return <li key={i}>{e}</li>
                        }
                    })}
                </ul>
            </div>
        </motion.div>
    )
}

