import React, {useEffect} from 'react'
import {Preview} from '../../components/Preview'
import {useDispatch, useSelector} from 'react-redux'
import Camera from '../../components/Camera'
import CustomForm from '../../components/CustomForm'
import {setAuthError, setScreenshot} from '../Login/authSlice'
import {setFacenetMessage, setOutline} from '../Login/facenetSlice'
import {AlertRegister} from '../../components/AlertRegister'

function Register() {
    const dispatch = useDispatch()
    const screenshot = useSelector((state) => state.auth.request.screenshot)
    const faces = useSelector((state) => state.facenet.faces)

    if (faces.length < 0) {
        dispatch(setAuthError({login: {serverErr: null}}))
        dispatch(setOutline('#ddd'))
        dispatch(setFacenetMessage('Place the face in the oval.'))
        dispatch(setScreenshot(null))
    }

    useEffect(() => {
        dispatch(setAuthError({login: {serverErr: null}}))
        dispatch(setOutline('#ddd'))
        dispatch(setFacenetMessage('Place the face in the oval.'))
        dispatch(setScreenshot(null))
    }, [dispatch])
    return (
        <section>
            <AlertRegister/>
            <div style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                gap: '2rem'
            }}>
                {!screenshot && <Camera/>}
                {screenshot && <Preview/>}
                {faces.length > 0 && <CustomForm/>}
            </div>
        </section>
    )
}

export default Register