import React from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import {AccountCircle, Mail, Password} from '@mui/icons-material'
import Typography from '@mui/material/Typography'
import Button from '@mui/material/Button'
import Link from '@mui/material/Link'
import {useDispatch, useSelector} from 'react-redux'
import {registerUser} from '../features/Login/authSlice'
import {useNavigate} from 'react-router-dom'
import {motion} from 'framer-motion'

function CustomForm() {
    const dispatch = useDispatch()
    const screenshot = useSelector((state) => state.auth.request.screenshot)
    const navigate = useNavigate()
    const faces = useSelector((state) => state.facenet.faces)
    const submitForm = (e) => {
        e.preventDefault()
        const {name, email, password} = e.target
        const user = {
            name: name.value,
            email: email.value,
            password: password.value,
            screenshot, descriptor: Object.values(faces[0].descriptor)
        }
        dispatch(registerUser(user)).then(payload => {
            if (payload.meta.requestStatus === 'fulfilled') {
                return navigate('/dashboard', {replace: true})
            }
        })
    }

    return (
        <motion.div
            initial={{opacity: 0, x: 100}}
            animate={{opacity: 1, x: 0}}
            exit={{opacity: 0, x: 100}}
            transition={{duration: 0.6}}
        >
            <Box
                component="form"
                onSubmit={submitForm}
                sx={{
                    '& > :not(style)': {my: 4},
                    backgroundColor: 'rgba(255,255,255,0.9)',
                    padding: '1rem',
                    borderRadius: '1rem',
                    width: '30vw'
                }}
                noValidate
                autoComplete="off"
            >
                <Typography variant="h4" sx={{
                    color: '#3c3c3c'
                }} align={'center'} gutterBottom>
                    Ro`yxatdan o`tish
                </Typography>
                <Box sx={{display: 'flex', alignItems: 'flex-end'}}>
                    <AccountCircle sx={{color: 'action.active', mr: 3, my: 0.5}}/>
                    <TextField fullWidth id="name" label="Name" variant="standard" size="small"/>
                </Box>
                <Box sx={{display: 'flex', alignItems: 'flex-end'}}>
                    <Mail sx={{color: 'action.active', mr: 3, my: 0.5}}/>
                    <TextField type={'email'} fullWidth id="email" label="Email" variant="standard" size="small"/>
                </Box>
                <Box sx={{display: 'flex', alignItems: 'flex-end'}}>
                    <Password sx={{color: 'action.active', mr: 3, my: 0.5}}/>
                    <TextField type={'password'} fullWidth id="password" label="Password" variant="standard"
                               size="small"/>
                </Box>
                {/*    submit button and link to login*/}
                <Box sx={{
                    display: 'flex',
                    justifyContent: 'flex-end',
                    alignItems: 'center',
                    gap: '2rem'
                }}>
                    <Link href="/login" underline="hover">
                        Kirish
                    </Link>
                    <Button type={'submit'} variant="contained">Ro`yxatdan o`tish</Button>
                </Box>
            </Box>
        </motion.div>
    )
}

export default CustomForm