import {useRef} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import * as faceapi from 'face-api.js'
import {detectFaces, setFacenetError} from '../features/Login/facenetSlice'

export const Preview = () => {
    const dispatch = useDispatch()
    const screenshot = useSelector((state) => state.auth.request.screenshot)
    const status = useSelector((state) => state.facenet.faces).length > 0

    const canvasRef = useRef()
    const previewRef = useRef()

    const handleScreenshot = async () => {
        dispatch(setFacenetError(null))
        dispatch(detectFaces({previewRef, canvasRef})).then(r => {
            const faces = r.payload
            if (faces && faces.length > 0) {
                let options = {
                    lineWidth: 4,
                    boxColor: (faces.length === 1 && faces[0].detection.score > 0.75) ? '#00ff00' : '#f00000',
                    drawLabelOptions: {fontColor: '#1e40af'}
                }
                faces.map(face => {
                    const box = face.detection.box
                    options['label'] = 'Score: ' + (Math.round(face.detection.score * 100) / 100)
                    const drawBox = new faceapi.draw.DrawBox(box, options)
                    return drawBox.draw(canvasRef.current)
                })
            }
        })
    }

    return (
        <div className={`preview ${status ? 'stopAnimate':''}`}>
            {
                screenshot != null && <>
                    <canvas id="preview-canvas" ref={canvasRef}>Your browser does not support the HTML
                        canvas tag.
                    </canvas>
                    <img ref={previewRef} src={screenshot} alt="preview" className={'preview'}
                         onLoad={handleScreenshot}/>
                </>
            }
        </div>
    )
}